    // 
    confirm() {
        if (this.selectedCar.length === 0) {
            this.messageService.clear();
            this.messageService.add({ severity: 'error', summary: '', detail: '' });
            return
        }
        this.canClick = false;
        let param = {
            custInfoList: this.selectedCar,
            marketingCampaignId: this.poolParam.marketingCampaignId,
            suggestContactCycle: this.poolParam.suggestContactCycle,
            receivePersonOrg: this.poolParam.receivePersonOrg,// 
            marketTopic: this.poolParam.marketTopic,
            instancyDegree: this.poolParam.instancyDegree,// 
            dataDt: this.poolParam.dataDt,
            custSource: this.poolParam.custSource,
            taskFlag: this.poolParam.taskFlag,
            custSearchFlag: '0',
            dataSource: this.poolParam.dataSource,
        }
        this.cusPoolHttpService.custSearchToCustPool(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                //  
                let modifyParam = {
                    operType: '2',
                    marketingCampaignId: this.poolParam.marketingCampaignId,
                    instancyDegree: this.poolParam.instancyDegree,// 
                    suggestContactCycle: this.poolParam.suggestContactCycle,// 
                    // receivePersonOrg: this.poolParam.receivePersonOrg,// 
                    marketTopic: this.poolParam.marketTopic,
                    dt: this.poolParam.dataDt,
                    custSource: this.poolParam.custSource,
                    dataSource: this.poolParam.dataSource,
                    agent: this.userId,
                    agentOrg: this.orgId,
                }
                let arr = data.loseList == null ? [] : data.loseList;
                let result = {
                    sucessNum: data.sucessNum,
                    loseList: arr,
                }
                let param = {
                    inputParams: modifyParam
                }
                this.cusPoolHttpService.modifyCustPoolPre(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.canClick = true;
                        this.commfunc.setSessionDataCH('poolMarketingCampaignIdResult', JSON.stringify(result))
                        this.commfunc.setSessionDataCH('poolMarketingCampaignId', this.poolParam.marketingCampaignId)
                        if (this.poolParam.taskFlag == '1') {
                            this.router.navigate(['/pages/tzb/custom/life-cycle/life-cycle-query/list-after-enter-the-pool']);
                        } else {
                            this.router.navigate(['/pages/tzb/custom/life-cycle/life-cycle-query/customer-allocation-pool']);
                        }
                    } else {
                        this.canClick = true;
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
                    }
                }, error => {
                    this.canClick = true;
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: '' });
                })
            } else {
                this.canClick = true;
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.canClick = true;
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: "" });
        });
    }

