import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { MessageService } from "primeng/components/common/messageservice";
import { CustomSearchHttpService } from "app/pages/tzb/custom/http/custom-search/custom-search.http.service";
import { Customtools } from "app/pages/tzb/custom/http/customUtil";
import { Commfunc } from "app/@uisftech/common/service/commonFun";

@Component({
  selector: 'app-master-recommend',
  templateUrl: './master-recommend.component.html',
  styleUrls: ['./master-recommend.component.scss'],
  providers: []
})
export class MasterRecommendComponent implements OnInit {
  data: any = [];

  loadingShow: any = { flag: false }; // loading

  params: any = {
    businessId: 'C0001',
    recommendType: '02'
  };
  configData: any = [];

  constructor(
    public router: Router,
    private messageService: MessageService,
    private httpService: CustomSearchHttpService,
    private commonFunc: Customtools,
    public commfunc: Commfunc,
  ) {

  }
  ngOnInit() {
    this.queryData(this.params, true);
    // this.queryData();


  }

  // 
  queryData(params, isMessage?) {
    const configParam = {
      httpService: this.httpService,
      loadingFlag: false,
      messageFlag: !isMessage,
      inputParam: params,
      message: this.messageService,
      injectName: 'conditionRecommendListQuery',
      loadingShow: this.loadingShow,
      messageInfo: '',
    }
    this.commonFunc.haddleData(configParam, (bodyData) => {
      if (bodyData) {
        if (bodyData.dataList.length > 4) { // 4 
          this.data = bodyData.dataList.splice(0, 4);
        } else {            // 4 
          this.data = bodyData.dataList;
        }

      }
    });
  }

  // 
  toConfig() {
    this.router.navigate(['pages/tzb/custom/custom-search/custom-search-config'], {
      queryParams: {
        configType: 'master',
        data: JSON.stringify(this.data)
      }
    });
  }


  // 
  selectCustom(data) {
    const strategyId = this.commfunc.compileStr(data.conditionId);
    this.router.navigate(['/pages/tzb/custom/custom-search/search-result-pages',], {
      queryParams: { strategyId: strategyId, strategyFlag: '2' }
    })
  }
  //
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }

}