  //     indicatorList: data,  // 
  //     outputFieldList: ['0321', '0327'],  // outputFieldList
  //     // sortField        // 
  //     pageNum: 1,
  //     pageSize: 10
  //   }
  //   console.log(params)
  //   const configParam = {
  //     httpService: this.httpService,
  //     loadingFlag: false,
  //     messageFlag: true,
  //     inputParam: params,
  //     message: this.messageService,
  //     injectName: 'advancedSearchExe',
  //     loadingShow: this.loadingShow,
  //     messageInfo: '',
  //   }
  //   this.commonFunc.haddleData(configParam, (bodyData) => {
  //     if (bodyData) {
  //       console.log(bodyData);

  //     }
  //   });
  // }



  // 
  add() {
    if (this.modelData.length < 10) { // 
      this.inputFlag = true;
    } else {
      this.messageService.clear();
      this.messageService.add({ severity: 'error', summary: '', detail: '' });
    }
  }

  // 
  delete(i) {
    this.modelData.splice(i, 1);
    if (this.modelData.length === 1) {  //   
      this.logicFlag = false;
    }
    if (this.modelData.length === 0) {  // 0  
      this.inputFlag = true;
    }
    this.selectFlag = true; // flagtrue
  }

  // 
  onItemSelect(event, i, optionName) {
    // debugger
    this.modelData[i].indicatorValue = '';
    this.modelData[i].indicatorValueCn = '';
    event.value.forEach((el, index) => {
      if (index === event.value.length - 1) {
        this.modelData[i].indicatorValue = this.modelData[i].indicatorValue + el;
      } else {
        this.modelData[i].indicatorValue = this.modelData[i].indicatorValue + el + ',';
      }
      this[optionName].forEach(ele => {
        if (ele.value === el) {
          this.modelData[i].indicatorValueCn = this.modelData[i].indicatorValueCn + ele.label + ','
        }

      });
    });
  }

  //  
  showModal(optionName, index) {
    // debugger
    // item ? this.inValueItem = item : this.inValueItem = '';
    this.modelIndex = index;
    this.selectdialogDisplay = true;
    this.inValueObj = this.modalObj[optionName];
    this.inValueItem = this.modelData[index].indicatorValue;
  }

  // 
  // selectKeyValue() {
  //   this.selectdialogDisplay = true;
  // }

  // 
  selectLogic() {
    this.inValueLogic = {
      key: this.logicMs,
      value: this.logicType
    }
    this.dialogDisplay = true;
    this.selectLogicFlag = true;
  }

  // 
  call(event) {
    this.dialogDisplay = false;
    this.selectLogicFlag = false;
    if (event) {
      this.logicType = event.value;
      this.logicMs = event.key;
      // this.logic = event;
    }
  }

  // 
  callInvest(event) {
    this.selectdialogDisplay = false;
    this.complicatedData = [];
    this.complicatedData = event;
    this.modelData[this.modelIndex].indicatorValueCn = '';
    this.modelData[this.modelIndex].indicatorValue = '';
    if (event.length != 0) {
      // modelData
      this.complicatedData.forEach((el, index) => {
        if (index === this.complicatedData.length - 1) {
          this.modelData[this.modelIndex].indicatorValueCn = this.modelData[this.modelIndex].indicatorValueCn + el.taskTypeName;
          this.modelData[this.modelIndex].indicatorValue = this.modelData[this.modelIndex].indicatorValue + el.taskTypeId;
        } else {
          this.modelData[this.modelIndex].indicatorValueCn = this.modelData[this.modelIndex].indicatorValueCn + el.taskTypeName + ',';
          this.modelData[this.modelIndex].indicatorValue = this.modelData[this.modelIndex].indicatorValue + el.taskTypeId + ',';
        }
      });
    }
  }

  // 
  callCustomGroup(event) {
    this.selectdialogDisplay = false;
    this.complicatedData = [];
    this.complicatedData = event;
    this.modelData[this.modelIndex].indicatorValueCn = '';
    this.modelData[this.modelIndex].indicatorValue = '';
    if (event.length != 0) {
      // modelData
      this.complicatedData.forEach((el, index) => {
        if (index === this.complicatedData.length - 1) {
          this.modelData[this.modelIndex].indicatorValueCn = this.modelData[this.modelIndex].indicatorValueCn + el.groupName;
          this.modelData[this.modelIndex].indicatorValue = this.modelData[this.modelIndex].indicatorValue + el.groupId;
        } else {
          this.modelData[this.modelIndex].indicatorValueCn = this.modelData[this.modelIndex].indicatorValueCn + el.groupName + ',';
          this.modelData[this.modelIndex].indicatorValue = this.modelData[this.modelIndex].indicatorValue + el.groupId + ',';
        }
      });
    }
  }

  // 
  callOptionalCustomGroup(event) {
    this.selectdialogDisplay = false;
    this.complicatedData = [];
    this.complicatedData = event;
    this.modelData[this.modelIndex].indicatorValueCn = '';
    this.modelData[this.modelIndex].indicatorValue = '';
    if (event.length != 0) {
      // modelData
      this.complicatedData.forEach((el, index) => {
        if (index === this.complicatedData.length - 1) {
          this.modelData[this.modelIndex].indicatorValueCn = this.modelData[this.modelIndex].indicatorValueCn + el.freeGroupName;
          this.modelData[this.modelIndex].indicatorValue = this.modelData[this.modelIndex].indicatorValue + el.freeGroupId;
        } else {
          this.modelData[this.modelIndex].indicatorValueCn = this.modelData[this.modelIndex].indicatorValueCn + el.freeGroupName + ',';
          this.modelData[this.modelIndex].indicatorValue = this.modelData[this.modelIndex].indicatorValue + el.freeGroupId + ',';
        }
      });
    }
  }

